import { Component, OnInit } from '@angular/core';

import { VendorService } from '../../../.././api/services/vendor.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-vendor-setup',
  templateUrl: './vendor-setup.component.html',
  styleUrls: ['./vendor-setup.component.scss']
})
export class VendorSetupComponent implements OnInit {

  vendorCategoryData:any;

  isVendorCategoryLoaded:boolean = false;
  isVendorCategorySubmitted:boolean = true;

  isVendorCategoryNew:boolean = false;
  isVendorCategoryUpdate:boolean = false;

  VendorCategory:string = "";
  VendorCategoryUpdateId:number = 0;
  
  alertMessage:string = "";
  isError:boolean = false; 

  vendorListData:any;

  constructor(
    private vendorService:VendorService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  addNewVendorCategory(){
  	this.isVendorCategoryNew = true;
  	this.isVendorCategoryUpdate = false;
  }

  updateVendorCategory(item){
  	this.isVendorCategoryUpdate = true;
  	this.isVendorCategoryNew = false;
  	this.VendorCategory = item.lookupValueName;
  	this.VendorCategoryUpdateId = item.lookupValueId;
  }

  deleteVendorCategory(item, index){

  	this.isVendorCategoryLoaded = false;

    console.log(item);
  	var params = {
      lookupValueId: item.lookupValueId,
      updateUserId: parseInt(this.cookieService.get('userId'))
    }

  	this.lookupService.deleteLookupvalue(params).subscribe((res:any) => {

  	  this.vendorCategoryData.splice(index, 1);
	  this.isVendorCategoryLoaded = true;

	});

  }

  removeCategoryBox(){
  	this.isVendorCategoryNew = false;
  	this.isVendorCategoryUpdate = false;
  	this.isError = false;
  }

  getVendorsCount(id){

    var data = this.vendorListData.filter( item => {
      return item.vendorCategoryId == id;
    });

    if(data === undefined || data.length === 0) {
      return 0
    }
    else {
      return data.length
    }

  }


  submitVendorCategoryForm(form) {

  	this.isVendorCategorySubmitted = false;

  	if(this.isVendorCategoryNew) {

  		let details = {

		  "lookupTypeId": 57, // Vendor category lookup id
		  "lookupValueName": this.VendorCategory,
		  "description": this.VendorCategory,
		  "isActive": true,
		  "insertedBy": parseInt(this.cookieService.get('userId')),
		  "insertedOn": "2019-11-20T16:51:24.105Z",
		  "updatedBy": 0,
		  "updatedOn": "2019-11-20T16:51:24.105Z"

		}

		this.lookupService.addLookupValue(details).subscribe((res:any) => {

	  		if(res.message) {
	  			
	  			this.lookupService.getLookupValueByLookupTypeId(57).subscribe((res:any) => {

	  				this.isVendorCategorySubmitted = true;
		          	this.sharedService.setAlertMessage("Category added Successfully!");
		          	
		          	this.vendorCategoryData = res.filter(item => {
	                	return item.isActive;
	              	});	

	  			});

	  			

	  		}
	  		else {

	  			this.isVendorCategorySubmitted = true;	
	          	this.isError = true;
	          	this.alertMessage = res.errorMessage;
	  		}

  		});

  	}

  	else {

  		let details = {

  		  "lookupValueId": this.VendorCategoryUpdateId,
		  "lookupTypeId": 57, // Vendor category lookup id
		  "lookupValueName": this.VendorCategory,
		  "description": this.VendorCategory,
		  "isActive": true,
		  "insertedBy": 0,
		  "insertedOn": "2019-11-20T16:51:24.105Z",
		  "updatedBy": parseInt(this.cookieService.get('userId')),
		  "updatedOn": "2019-11-20T16:51:24.105Z"

		}

		this.lookupService.updateLookupValue(details).subscribe((res:any) => {

	  		if(res.message) {
	  			
	  			this.lookupService.getLookupValueByLookupTypeId(57).subscribe((res:any) => {

	  				this.isVendorCategorySubmitted = true;
	  				this.sharedService.setAlertMessage("Category updated Successfully!");
		          	
		          	this.vendorCategoryData = res.filter(item => {
	                	return item.isActive;
	              	});	

	  			});

	  			

	  		}
	  		else {

	  			this.isVendorCategorySubmitted = true;	
	          	this.isError = true;
	          	this.alertMessage = res.errorMessage;
	  		}

  		});

  	}

  	

  }

  ngOnInit() {

  	//vendor category
  	this.lookupService.getLookupValueByLookupTypeId(57).subscribe((res:any) => {

  		this.vendorCategoryData = res.filter(item => {
        	return item.isActive;
      });

      this.vendorService.getVendorByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

        this.vendorListData = res;
        this.isVendorCategoryLoaded = true;

      },
      error => {
        
      });


  	});


}

}
