import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { AccountsService } from '../../../.././api/services/accounts.service';
import { VendorService } from '../../../.././api/services/vendor.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-create-vendor',
  templateUrl: './create-vendor.component.html',
  styleUrls: ['./create-vendor.component.scss']
})
export class CreateVendorComponent implements OnInit {

  vendor:any;

  vendorCategoryList:any;
  vendorTypeList:any;
  glAccountListData:any;
  
  isEditVendor:boolean = false;

  legalType:string = "";
  vendorTypeId:string = "";
  vendorStatus:boolean = true;

  isVendorSubmitted:boolean = false;
  isVendorAdded:boolean = false;

  isError:boolean = false;
  alertMessage:string = "";

  constructor(
    private router: Router,
    private route: ActivatedRoute,
  	private accountsService:AccountsService,
    private vendorService:VendorService,
    private apartmentService:ApartmentService,
    private lookupService:LookupService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
 ) { }

  
  getCategoryName(){

  }

  submitAddVendorForm(form){

    this.isVendorSubmitted = true;
    this.isError = false;

    if(!this.isEditVendor) {

      let details = {

        "vendorTypeId": parseInt(this.vendorTypeId),
        "vendorName": this.vendor.vendorName,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "legalTypeId": parseInt(this.vendor.legalTypeId),
        "vatorTin": this.vendor.vatorTin,
        "tax1": this.vendor.tax1 || " ",
        "tax2": this.vendor.tax2 || " ",
        "tax3": this.vendor.tax3 || " ",
        "tax4": this.vendor.tax4 || " ",
        "contactPerson": this.vendor.contactPerson,
        "email": this.vendor.email || " ",
        "phone1": this.vendor.phone1 || " ",
        "phone2": this.vendor.phone2 || " ",
        "fax": this.vendor.fax,
        "address1": this.vendor.address1 || " ",
        "address2": "string",
        "city": "string",
        "state": "string",
        "pin": "string",
        "vendorCategoryId": parseInt(this.vendor.vendorCategoryId),
        "vendorStatus": this.vendorStatus,
        "notes1": this.vendor.notes1 || " ",
        "notes2": this.vendor.notes2 || " ",
        "glaccountTypeId": parseInt(this.vendor.glaccountTypeId),
        "bankAccountDetailsId": null,
        "isActive": this.vendorStatus,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-11-08T07:28:27.787",
        "updatedBy": null,
        "updatedOn": null

      }

      this.vendorService.addVendor(details).subscribe((res:any) => {

        if(res.message) {

          this.isVendorSubmitted = false;
          this.sharedService.setAlertMessage("Vendor added successfully");
          this.router.navigate(['ams/vendor/all-vendors', parseInt(this.vendor.vendorCategoryId)]);

        }
        else {
          this.isVendorSubmitted = false;
          this.isError = true;
          this.alertMessage = res.errorMessage;
        }

      },
      error => {
        this.isVendorSubmitted = false;
        this.isError = true;
        this.alertMessage = "Some error occured";
      });

    }

    else {

        let details = {

          "vendorId": parseInt(this.vendor.vendorId),
          "vendorTypeId": parseInt(this.vendorTypeId),
          "vendorName": this.vendor.vendorName,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "legalTypeId": parseInt(this.vendor.legalTypeId),
          "vatorTin": this.vendor.vatorTin,
          "tax1": this.vendor.tax1 || " ",
          "tax2": this.vendor.tax2 || " ",
          "tax3": this.vendor.tax3 || " ",
          "tax4": this.vendor.tax4 || " ",
          "contactPerson": this.vendor.contactPerson,
          "email": this.vendor.email || " ",
          "phone1": this.vendor.phone1 || " ",
          "phone2": this.vendor.phone2 || " ",
          "fax": this.vendor.fax,
          "address1": this.vendor.address1 || " ",
          "address2": "string",
          "city": "string",
          "state": "string",
          "pin": "string",
          "vendorCategoryId": parseInt(this.vendor.vendorCategoryId),
          "vendorStatus": this.vendorStatus,
          "notes1": this.vendor.notes1 || " ",
          "notes2": this.vendor.notes2 || " ",
          "glaccountTypeId": parseInt(this.vendor.glaccountTypeId),
          "bankAccountDetailsId": null,
          "isActive": this.vendorStatus,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": "2019-11-08T07:28:27.787",
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": null

        }

        this.vendorService.updateVendor(details).subscribe((res:any) => {

          if(res.message) {

            this.isVendorSubmitted = false;
            this.sharedService.setAlertMessage("Vendor updated successfully");
            this.router.navigate(['ams/vendor/all-vendors', parseInt(this.vendor.vendorCategoryId)]);

          }
          else {
            this.isVendorSubmitted = false;
            this.isError = true;
            this.alertMessage = res.errorMessage;
          }

        },
        error => {
          this.isVendorSubmitted = false;
          this.isError = true;
          this.alertMessage = "Some error occured";
        });


      }


  }

  ngOnInit() {

  	this.vendor = {};
  	this.vendor.glaccountTypeId = "";
  	this.vendor.vendorCategoryId = "";
  	this.vendor.legalTypeId = "";
  	this.vendorTypeId = "";

     if(this.route.params['value'].id != undefined) {
      
      this.isEditVendor = true;  
      this.isVendorSubmitted = true;

      this.vendorService.getVendorById(this.route.params['value'].id).subscribe((res:any) => {
          
        this.vendor = res[0];
        this.vendorTypeId = ""+this.vendor.vendorTypeId;

        this.isVendorSubmitted = false;


      },
      error => {
        
      }
      );


    }

    var accountParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      groupId: 3
    }

    this.accountsService.getGLAccountsByGroupId(accountParams).subscribe((res:any) => {

  		this.glAccountListData = res;

  	});

  	//vendor Category 
    this.lookupService.getLookupValueByLookupTypeId(57).subscribe((res:any) => {
          
      this.vendorCategoryList = res.filter(item => {
      	return item.isActive;
      });

    },
    error => {
      
    }
    );

    //vendor type 
    this.lookupService.getLookupValueByLookupTypeId(59).subscribe((res:any) => {
          
      this.vendorTypeList = res.filter(item => {
      	return item.isActive;
      });

    },
    error => {
      
    }
    );

  }

}
