import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { VendorComponent } from './vendor.component';
import { VendorSetupComponent } from './components/vendor-setup/vendor-setup.component';
import { CreateVendorComponent } from './components/create-vendor/create-vendor.component';
import { VendorReportsComponent } from './components/vendor-reports/vendor-reports.component';
import { AllVendorsComponent } from './components/all-vendors/all-vendors.component';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: VendorSetupComponent},
	{ path: 'all-vendors', component: AllVendorsComponent},
	{ path: 'all-vendors/:id', component: AllVendorsComponent},
	{ path: 'create-vendor', component: CreateVendorComponent},
	{ path: 'edit-vendor/:id', component: CreateVendorComponent},
	{ path: 'reports', component: VendorReportsComponent},
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VendorRoutingModule { }
