import { Component, OnInit, Injector, ViewChild, TemplateRef } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { VendorService } from '../../../.././api/services/vendor.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-all-vendors',
  templateUrl: './all-vendors.component.html',
  styleUrls: ['./all-vendors.component.scss']
})
export class AllVendorsComponent implements OnInit {

  vendorCategoryData:any;
  vendorCategoryId:string = "";

  isVendorCategoryLoaded:boolean = false;

  isVendorCategorySelected:boolean = false;
  isVendorLoaded:boolean = true;

  vendorData:string = "";
  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;
  totalFilterItems:any;

  modalService:any;

  vendor:any;

  vendorListData:any;

  categoryName:string = "";

  selectedInput:string = "";
  columnField: any = {}

  @ViewChild("viewVendorRef", { static: false }) viewVendorRef: TemplateRef<any>;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private injector: Injector,
    public dialog: MatDialog,
    private vendorService:VendorService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) {
    this.modalService = this.injector.get(ModalService);
   }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }


  getVendorCategoryName(id){
  	  
  	_.each(this.vendorCategoryData, (item, index) => {

  		if(item.lookupValueId == parseInt(id)){
  			this.categoryName = item.lookupValueName;
  		}

      this.isVendorCategorySelected = true;


  	});

    if(id == "All"){
      this.categoryName = "";
    }

    this.isVendorLoaded = false;

    this.vendorService.getVendorByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

        if(id != "All"){
          this.vendorListData = res.filter(item => {
            return item.vendorCategoryId == parseInt(id);
          });
        }
        else {
          this.vendorListData = res;
        }
        

        this.isVendorLoaded = true;

        this.totalItems = this.vendorListData.length;

        if(this.totalItems>this.itemLimit){
          this.ItemEndIndex = this.itemLimit;
        }

        else {
          this.ItemEndIndex = this.totalItems;
        }

    },
    error => {
      
    });

  }

  showConfirmModal(id) {
    this.modalService.showConfirmModal(id);
  }

  showVendorInfo(vendor){
    
    this.dialog.open(this.viewVendorRef, {
        panelClass: 'material',
        disableClose: true,
        data: vendor
    });

  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  ngOnInit() {

  	this.vendor = {};
    this.vendorCategoryId = "All";
    this.vendor.categoryName = "";


  	this.lookupService.getLookupValueByLookupTypeId(57).subscribe((res:any) => {

  		this.isVendorCategoryLoaded = true;

  		this.vendorCategoryData = res.filter(item => {
        	return item.isActive;
      });

      this.getVendorCategoryName(this.vendorCategoryId);


      if(this.route.params['value'].id != undefined) {
          
        this.vendorCategoryId = ""+this.route.params['value'].id;
        this.getVendorCategoryName(this.vendorCategoryId);

      }


  	});




    // delete vendor
    this.sharedService.unitlistdeleteindexcast.subscribe( id => {

      if(id != null){

        var params = {
          vendorId: id,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }


        this.vendorService.deleteVendor(params).subscribe((res:any) => {

           _.each(this.vendorListData, (type)=>{
            if(type.vendorId == id){
              type.isActive = false;
            }
          })

          setTimeout(() => {

            this.vendorListData = this.vendorListData.filter((type) => type.vendorId !== id);
            this.totalItems = this.vendorListData.length;
            this.sharedService.setAlertMessage("Vendor deleted");
            this.sharedService.setUnitListDeleteIndex(null);

          }, 500)


        }, error => {
          console.log(error);
        });

        
      }

    });


  }


}
